/**
 * Banner
 */
.banner {
  position: relative;
  min-width: 360px;
  max-width: 1500px;
  min-height: 200px;
  margin: auto;
  padding: 50px 10%;
  text-align: center;
  box-sizing: border-box;
}

.banner>h2 {
  margin: 0 0 30px;
}

.banner>p {
  color: #474747;
}

.faded-in {
  animation: fade-in 0.5s ease-out;
}

/* Fade-in animation */
@keyframes fade-in {
  0% {
    opacity: 0;
    top: 100px;
  }

  100% {
    opacity: 1;
    top: 0;
  }
}

@media only screen and (min-width: 768px) {
  .banner {
    box-sizing: inherit;
  }

  .banner:not(.showcase):not(.logos) {
    padding: 50px 20%;
  }
}

/* 20x + 1500 + 20x = 100x => 100x = 2500  */
@media only screen and (min-width: 2500px) {
  .banner {
    padding: 50px calc((100% - 1500px) / 2);
  }
}

/* 20x + 1500 + 20x = 100x => 100x = 2500  */
@media only screen and (min-width: 1800px) {
  .showcase {
    padding: 50px calc((100% - 1500px) / 2);
  }
}

/**
 * Hero
 */
section.banner.hero {
  position: relative;
  display: flex;
  justify-content: center;
  padding: 130px 10%;
  width: stretch;
  max-width: 100vw;
  top: 0;
  background: #50bebe;
  overflow: hidden;
  z-index: 1000;
  transition: height 0.3s ease;
  box-sizing: border-box;
}

.hero-content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: inherit;
  height: inherit;
  max-width: 1800px;
  padding: 0 10%;
}

.hero img {
  position: relative;
  max-width: 80vw;
  max-height: 80vw;
  border-radius: 100%;
  border: 3px solid #b3ddd9;
  box-shadow: 5px 5px 10px rgba(2, 23, 33, 0.12);
}

.conversation {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 20px;
}

.hero .greeting {
  z-index: 10;
}

.hero .action {
  margin-left: auto;
}

.speech-bubble {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 100%;
  height: 70px;
  margin: 10px 0;
  padding: 0 10px;
  border-radius: 10px;
  background: #fbfeff;
  box-sizing: border-box;
  box-shadow: 5px 5px 10px rgba(2, 23, 33, 0.12);
}

/* Ensure that the speech bubble contents are in front of the bubble arrow */
.speech-bubble * {
  z-index: 5;
}

.speech-bubble h1 {
  font-size: 1.2em;
  white-space: nowrap;
}

/* After 450px, we can increase the header font size */
@media only screen and (min-width: 450px) {
  .speech-bubble h1 {
    font-size: 1.5em;
  }
}

/* Between widths of 900 and 1000 px, the header font size is temporarily too large */
@media only screen and (min-width: 900px) and (max-width: 1000px) {
  .speech-bubble h1 {
    font-size: 1.2em;
  }
}

/* Greeting bubble arrow */
.hero .greeting:after {
  content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 0;
	height: 0;
	border: 20px solid transparent;
	border-right-color: #fbfeff;
	border-left: 0;
	border-top: 0;
	margin-top: 0;
	margin-left: -12px;
}

/* Action bubble arrow */
.hero .action:after {
	content: '';
	position: absolute;
  right: 0;
  bottom: 0;
	width: 0;
	height: 0;
	border: 30px solid transparent;
	border-left-color: #fbfeff;
	border-right: 0;
	border-bottom: 0;
	margin-top: 0;
	margin-right: -12px;
}

/* Devices with a larger width get a header animation */
@media only screen and (min-width: 900px) {
  section.banner.hero {
    padding: 0;
    height: 500px;
  }

  .hero-content {
    flex-direction: row;
    justify-content: center;
  }

  .hero img {
    width: 400px;
    height: 400px;
  }

  .conversation {
    flex-direction: row;
    flex-wrap: wrap;
    width: auto;
    margin-top: 0;
  }
  
  .speech-bubble {
    width: calc(40vw - 120px);
    max-width: 780px; /* 1800px / 2 - 120px */
    margin: 10px;
  }
}

/* Devices with larger width & height get a sticky header */
@media only screen and (min-width: 900px) and (min-height: 700px) {
  section.banner.hero  {
    position: sticky;
    top: -400px;
  }

  .hero-content {
    position: fixed;
    top: 0;
  }
}

/* We need to disable the max-height set from the Hero component on smaller screens */
@media only screen and (max-width: 899.999px) {
  .hero-content {
    max-height: none !important;
  }
}

/**
 * Paragraph
 */
.paragraph h2 {
  text-align: left;
}

.paragraph p {
  text-align: justify;
}

.paragraph-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

/* From medium screen widths, we can display the title and image on the same line */
@media only screen and (min-width: 450px) {
  .paragraph-header {
    flex-direction: row;
  }

  .paragraph-header img {
    margin-left: 20px;
  }
}

/**
 * Showcase
 */
.showcase {
  background: #f2f6f9;
  padding-bottom: 80px;
}

.showpieces a {
  color: rgb(43, 43, 43);
  text-decoration: none;
}

.showpiece p {
  color: #474747;
}

.showpieces {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.showpiece {
  position: relative;
  top: 0;
  margin: 20px;
  width: 300px;
  height: 400px;
  padding: 10px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 8px 14px 38px rgba(45, 51, 56, 0.08), 1px 3px 8px rgba(45, 51, 56, 0.03);
  transition: 0.5s ease;
}

/* TODO only on desktop */
.showpiece:hover {
  top: -10px;
}

.showpiece img {
  width: 300px;
  height: 200px;
  object-fit: cover;
}

/**
 * Logos
 */
.logo-collection {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.logos a {
  align-self: center;
}

.logos img {
  width: 200px;
  padding: 50px 30px;
  transition: 0.5s ease;
}

.logos img:hover {
  transform: scale(1.2);
}

/**
 * Footer
 */
.footer {
  max-width: initial;
  min-height: 0;
  padding-top: 80px !important;
  padding-bottom: 120px !important;
  background: #f2f6f9;
}

/**
 * Social buttons
 */
.social-button {
  display: inline-block;
  margin: 10px;
  font-size: 1rem;
  font-weight: 500;
  padding: 3px 10px;
  border-radius: 5px;
  border: none;
  background: #b3ddd9;
  text-decoration: none;
  color: #474747;
  white-space: nowrap;
}

.social-button:focus {
  animation: wave 0.2s ease-in;
}

.social-button i {
  margin-right: 6px;
}

.social-button:hover i {
  color: #7e228e;
  transition: all 0.2s ease;
}

/* Button click animation */
@keyframes wave {
  0% {
    box-shadow: 0 0 0 0 #50bebe33;
  }

  50% {
    box-shadow: 0 0 0 3px #50bebe33;
  }

  100% {
    box-shadow: 0 0 0 4px #50bebe00;
  }
}
